<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <div>
    <!-- TODO This should be just once on the page (idem BasicMdEntry) -->
    <Notifications :alerts="alerts" :floating="true" />

    <div v-if="!restCallsGoing">
      <v-icon v-if="entryHtml===null">mdi-account-alert</v-icon>
      <span v-else v-html="entryHtml" />
    </div>
    <v-progress-circular v-else indeterminate style="margin: 15px 0 0 130px" />

  </div>
</template>

<script>
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    props: ['sourcePath', 'withCredentials'],
    components: {Notifications},
    mixins: [AxiosHelper],

    data: () => ({
      entryHtml: ''
    }),

    mounted () {
      (this.withCredentials && this.credentialedAxios || this.plainAxios)(
        'reading ' + this.sourcePath,
         axios => axios.get(this.sourcePath),
         data => this.entryHtml = data);
    }
  }
</script>
