<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <div>
    <v-container>
      <!-- These padding tweaks are very annoying -->
      <v-list style="padding: 4px 4px 0px" dense nav>
        <v-list-item v-for="button in categories"
                     style="display: inline-block;width: 220px;padding: 10px;text-align: center;"
                     link :to="'/l/' + button">
          <span>{{ showLanguageSlider && englishCategoryNames[button] || button }}</span>
        </v-list-item>

        <v-progress-circular indeterminate style="width: 220px" v-if="!!restCallsGoing" />

        <span class="floating-controls">
          <v-icon @click="widthSlider = 0">mdi-arrow-collapse-horizontal</v-icon>
          <v-slider class="floating-controls-slider" v-model="widthSlider"
                    min="0" step="1" max="100" />
          <v-icon @click="widthSlider = 100">mdi-arrow-expand-horizontal</v-icon>
        </span>
        <span class="floating-controls"
              v-if="showLanguageSlider" >
          <span class="flag-button" @click="languageSlider = 0">🇬🇧</span>
          <v-slider class="floating-controls-slider" v-model="languageSlider"
                    min="0" step="1" max="2" />
          <span class="flag-button" @click="languageSlider = 2">🇷🇴</span>
        </span>

      </v-list>
    </v-container>
    <v-container fluid :style="{'max-width': viewerMaxWidth}">

      <Notifications :alerts="alerts" />

      <LiteratureCollection v-if="!!rootCollection"
                            :category="category" :collection="rootCollection" />
    </v-container>
  </div>
</template>

<script>
  import router from '../plugins/router.js';
  import AxiosHelper from '../components/AxiosHelper.vue';
  import LiteratureCollection from '../components/LiteratureCollection.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {LiteratureCollection, Notifications},
    mixins: [AxiosHelper],

    props: ['category'],

    data: () => ({

      englishCategoryNames: {
        'Eseuri': 'Essays',
        'Poezii': 'Poetry'
      },

      widthSlider: 0,
      languageSlider: 1,

      staticRootCollections: {},
      loadedCategories: [],
      loadedRootCollection: null
    }),

    computed: {
      viewerMaxWidth () { // Minimum 675, maximum 1875, where it becomes null i.e. no limit
        return this.widthSlider < 100
            && ((675 + Math.max(this.widthSlider, 0) * 12) + 'px')
            || null;
      },

      showLanguageSlider () {
        // TODO This logic
        return false;
      },

      categories () {
        let set = new Set(this.loadedCategories || []);
        Object.keys(this.staticRootCollections).forEach(key => set.add(key));
        return set;
      },

      rootCollection () {
        let safeStaticEntries = (this.staticRootCollections && this.staticRootCollections[this.category] && this.staticRootCollections[this.category].entries || []);
        let safeStaticSubCollections = (this.staticRootCollections && this.staticRootCollections[this.category] && this.staticRootCollections[this.category].subCollections || []);

        let safeLoadedEntries = (this.loadedRootCollection && this.loadedRootCollection.entries || []);
        let safeLoadedSubCollections = (this.loadedRootCollection && this.loadedRootCollection.subCollections || []);

        return this.absolutisePaths({
          title: '(root)',
          entries: safeStaticEntries.concat(safeLoadedEntries),
          subCollections: safeStaticSubCollections.concat(safeLoadedSubCollections)
        });
      }
    },

    methods: {
      loadTocIfNeeded() {
        if (this.category)
          this.credentialedAxios('loading TOC', axios => axios.get(this.$apiUrl + '/l/toc/' + encodeURIComponent(this.category)),
            data => this.loadedRootCollection = data);
      },

      absolutisePaths (collection) {
        if (!(collection && (collection.entries || collection.subCollections)))
          return collection;

        let entries = (collection.entries || []).map(e =>
          Object.assign({}, e, {
            htmlPath: this.absolutiseServerPath(e.htmlPath),
            mdPath: this.absolutiseServerPath(e.mdPath),
            pdfPath: this.absolutiseServerPath(e.pdfPath),
            staticHtmlPath: this.absolutiseStaticPath(e.staticHtmlPath),
            staticMdPath: this.absolutiseStaticPath(e.staticMdPath),
            staticPdfPath: this.absolutiseStaticPath(e.staticPdfPath)
          }));

        let subCollections = (collection.subCollections || []).map(this.absolutisePaths);
        return Object.assign({}, collection, {entries, subCollections});
      },

      absolutiseStaticPath(path) {
        return path && this.$dataUrl + '/l/' + path;
      },

      absolutiseServerPath(path) {
        return path && this.$apiUrl + '/l/toc/' + encodeURIComponent(this.category) + '/' + encodeURIComponent(path);
      },

      navigateToFirstCategoryIfNeeded () {
        if (!this.category && !!this.categories.size) {
          // This preposterous set idiom again...
          router.replace('/l/' + this.categories.entries().next().value[0]);
        }
      }
    },

    watch: {
      category() {
        this.loadTocIfNeeded();
      }
    },

    beforeMount () {
      Promise.all([
        this.credentialedAxios('loading categories', axios => axios.get(this.$apiUrl + '/l/categories'),
          data => this.loadedCategories = data.d || []),
        this.plainAxios('loading static index', axios => axios.get(this.$dataUrl + '/l/index.json'),
          data => this.staticRootCollections = data)
      ])
        .then(this.navigateToFirstCategoryIfNeeded);
    },

    mounted () {
      this.loadTocIfNeeded();
    }
  }
</script>

<style>
  .outer-panel > .v-expansion-panel-content__wrap {
    padding-left: 0;
    padding-right: 0;
  }
  .flag-button {
    display: inline-block;
    width: 24px;
    height: 24px;
    cursor: pointer;
    vertical-align: middle;
  }
  .floating-controls {
    float: right;
    margin-top: -4px;
    padding-left: 20px;
  }
  .floating-controls-slider {
    display: inline-block;
    width: 80px;
    transform: translateY(10px);
  }
  /* Copied what Vue gave for min-height and added opacity * /
  TODO Reenable these (perhaps with better classes) when we can regulate the presence of titles _in_ the content
  .v-expansion-panel-header {
    transition: opacity,min-height .3s cubic-bezier(.25,.8,.5,1);
  }
  / * Make text in the accordion handle invisible when the entry is showing, because it begins with that very same title * /
  .v-expansion-panel-header--active {
    opacity: 0;
  }*/
</style>
