<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-expansion-panels popout v-model="expanded">

    <v-expansion-panel
          hover focusable popout
          v-for="subCollection in collection.subCollections"
          :key="subCollection.title">
      <!-- (Above) Title is better than index, but there's still a slim possibility of collision with an entry with the same title in another category -->

      <v-expansion-panel-header>
        📖&ensp;{{ subCollection.title }}
      </v-expansion-panel-header>

      <v-expansion-panel-content class="outer-panel">
        <LiteratureCollection :category="category" :collection="subCollection" />
      </v-expansion-panel-content>
    </v-expansion-panel>

    <v-expansion-panel
          hover focusable popout
          v-for="(entry,i) in collection.entries"
          :key="entry.title">

      <v-expansion-panel-header>
        🖋&ensp; {{ (entry.stickyTitle || !isCurrentlyExpanded(i)) && entry.title || '' }}

        <span v-if="entry.lang === 'en'"
              style="text-align: right;">
          🇬🇧
        </span>
        <span v-if="entry.lang === 'ro'"
              style="text-align: right;">
          🇷🇴
        </span>
      </v-expansion-panel-header>

      <v-expansion-panel-content>
        <HtmlEntry v-if="entry.htmlPath" :sourcePath="entry.htmlPath" :withCredentials="true" />
        <HtmlEntry v-if="entry.staticHtmlPath" :sourcePath="entry.staticHtmlPath" :withCredentials="false" />
        <BasicMdEntry v-if="entry.mdPath" :sourcePath="entry.mdPath" :withCredentials="true" />
        <BasicMdEntry v-if="entry.staticMdPath" :sourcePath="entry.staticMdPath" :withCredentials="false" />
        <!-- TODO How to load iframe with credentials? -->
        <iframe class="pdf-entry" v-if="entry.pdfPath" :src="entry.pdfPath" />
        <iframe class="pdf-entry" v-if="entry.staticPdfPath" :src="entry.staticPdfPath" />
      </v-expansion-panel-content>
    </v-expansion-panel>

  </v-expansion-panels>
</template>

<script>
import BasicMdEntry from '../components/BasicMdEntry.vue';
import HtmlEntry from '../components/HtmlEntry.vue';

export default {
    name: 'LiteratureCollection',
    components: {BasicMdEntry, HtmlEntry},
    props: ['category', 'collection'],

    data: () => ({
      expanded: null
    }),

    methods: {
      isCurrentlyExpanded(i) {
        return this.expanded === (this.collection.subCollections.length || 0) + i;
      }
    },

    mounted() {
      console.log('LiteratureCollection :: this.collection =', this.collection);
    }
  }
</script>
<style>
.pdf-entry {
	border: none;
	width: calc(100% + 20px);
	height: 810px;
	max-height: 90vh;
	margin: -10px;
}
</style>
